import React, { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import QvipleLoading from "../../../../../Loader/QvipleLoading";
import CertificateCard from "../../../../student/Mainbody/StudentCertificate/CertificateCard";
import {
  useCertificateAuthorityExportExcel,
  useGetAllCertificateAuthority,
} from "../../../../../hooks/member_tab/department-api";
import { useTranslation } from "react-i18next";
import { useNavigate, useParams } from "react-router-dom";
import style from "../../PromoteDepartment/PromoteDepartment.module.css";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import QLoader from "../../../../../Loader/QLoader";
const CertificateIssued = ({ instituteId, carryParentState }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const params = useParams();
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [certificateList, setCertificateList] = useState([]);
  const [disabled, setDisabled] = useState(false);

  const [certificateAuthorityExportExcel] =
    useCertificateAuthorityExportExcel();
  const { getAllCertificateAuthority, getAllCertificateAuthorityRefetch } =
    useGetAllCertificateAuthority({
      data: {
        id: instituteId,
        status: "Issued",
        page: page,
        limit: 10,
      },
      skip: !instituteId,
    });
  useEffect(() => {
    if (instituteId) {
      setShowingDataLoading(true);
      getAllCertificateAuthorityRefetch();
    }
  }, [instituteId, page, getAllCertificateAuthorityRefetch]);

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (getAllCertificateAuthority?.all_cert) {
      if (page === 1) {
        setCertificateList(getAllCertificateAuthority?.all_cert);
        setShowingDataLoading(false);
      } else {
        setCertificateList((prevState) =>
          [
            ...new Set(
              [...prevState, ...getAllCertificateAuthority?.all_cert]?.map(
                JSON.stringify
              )
            ),
          ]?.map(JSON.parse)
        );
        setShowingDataLoading(false);
      }
    } else if (getAllCertificateAuthority?.all_cert?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    if (getAllCertificateAuthority?.all_cert?.length === 10) setState(true);
    else setState(false);
  }, [getAllCertificateAuthority?.all_cert]);

  const onExportExcel = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      certificateAuthorityExportExcel({
        id: instituteId,
        export: {
          flow: "Issued",
        },
      })
        .then(() => {
          navigate(`/q/${params.username}/member/certificate?a=export`, {
            state: carryParentState,
          });
        })
        .catch({});
    }
  };
  return (
    <>
      <div className={style.with_search}>
        <h6>{t("certificate_issued")}</h6>
        <div
          style={{
            display: "flex",
            gap: "1rem",
            alignItems: "center",
          }}
        >
          <img
            src="/images/member_tab/finance/finance-export-icon.svg"
            style={{
              cursor: "pointer",
              height: "1.6rem",
            }}
            onClick={onExportExcel}
            alt="lock icon"
          />
        </div>
      </div>
      <BorderBottom
        customStyle={{
          width: "100%",
          marginTop: "0.5rem",
        }}
      />
      {certificateList?.map((certificate, index) =>
        certificateList?.length === index + 1 ? (
          <div ref={ref} key={certificate?._id}>
            <CertificateCard
              certificate={certificate}
              isViewBy="ALREADY_ISSUED"
            />
          </div>
        ) : (
          <CertificateCard
            certificate={certificate}
            key={certificate?._id}
            isViewBy="ALREADY_ISSUED"
          />
        )
      )}
      {showingDataLoading && <QvipleLoading />}
      {disabled && <QLoader />}
    </>
  );
};

export default CertificateIssued;
