import CertificateLogsCard from "./CertificateLogsCard";
import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useCertificateHistoryLogs } from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";
import CertificateLogsIssueMenu from "./CertificateLogsIssueMenu";
const CertificateLogs = ({ instituteId }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { certificateHistoryLogs, certificateHistoryLogsLoading } =
    useCertificateHistoryLogs({
      data: {
        id: instituteId,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !instituteId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (certificateHistoryLogs?.list?.length) {
      let obj = nextPage(certificateHistoryLogs?.list?.length);
      setState(() => obj);
    }
  }, [certificateHistoryLogs?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("certificate_logs")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label="issue_certificate"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {certificateHistoryLogs?.list?.map((d_log, index) =>
            certificateHistoryLogs?.list?.length === index + 1 ? (
              <div key={d_log?._id} ref={ref}>
                <CertificateLogsCard d_log={d_log} />
              </div>
            ) : (
              <CertificateLogsCard d_log={d_log} key={d_log?._id} />
            )
          )}

          {certificateHistoryLogsLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            certificateHistoryLogs?.list?.length === 0 && (
              <EmptyMessage
                title="No item found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            certificateHistoryLogs?.list?.length === 0 && (
              <EmptyMessage
                title="No item."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {add && (
        <CertificateLogsIssueMenu
          onClose={onToggle}
          instituteId={instituteId}
        />
      )}
    </>
  );
};

export default CertificateLogs;
