import { useEffect, useState } from "react";
import { useCertificateLeavingFormSetting } from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
const CertificateSetting = ({ instituteId, masterData }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    certificate_leaving_form_student: "",
  });

  const [certificateLeavingFormSetting] = useCertificateLeavingFormSetting();

  useEffect(() => {
    if (masterData?.certificate_leaving_form_student) {
      setState((prev) => ({
        ...prev,
        certificate_leaving_form_student:
          masterData?.certificate_leaving_form_student,
      }));
    }
  }, [masterData?.certificate_leaving_form_student]);
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };

  const onSave = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      certificateLeavingFormSetting({
        id: instituteId,
        bodyParameter: state,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("setting")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        <CustomSelect
          selectLabel={t("leaving_certificate_form")}
          selectedValue={t("leaving_certificate_form_placeholder")}
          defalutValue={state.certificate_leaving_form_student}
          onClick={(val) =>
            onSelectDropdown(val, "certificate_leaving_form_student")
          }
          options={["Yes", "No"]}
          customStyleContainer={{
            width: "49%",
          }}
          customListStyle={{
            position: "absolute",
            width: "100%",
            zIndex: "1",
          }}
        />
        <CreateButton label="save" onAction={onSave} />
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </UserMemberContainerWrapper>
  );
};

export default CertificateSetting;
